body{
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url("./images/bgimg3.jpg");
  // background:linear-gradient(gray,white,gray);  
  background-size: cover;
}
code{
  font-family: Roboto,source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;}


  /* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
@media screen and (min-device-width: 0) and (max-device-width: 1200px) { 
  body{
    background-size:cover;
    background-repeat: no-repeat;
    background: url("./images/bgimg3sm.jpg");  
  }
}
